import { template } from 'underscore'
import $ from 'jquery'
import CheckBoxTemplate from '../html/ParseAsDelimitedFileTemplate.html'
import '../stylesheets/ParseAsDelimitedFileInput.css'


class ParseAsDelimitedFileInput {
  /**
     *
     * @constructor
     * @param {Object} globalConfig - Global configuration.
     * @param {element} el - The element of the custom row.
     * @param {string} data - Service name.
     * @param {object} util - the utility object
     * @param {function} setValue - set value of the custom field
     */
  constructor (globalConfig, el, data, setValue, util) {
    this.globalConfig = globalConfig
    this.el = el
    this.data = data
    this.util = util
    this.setValue = setValue
  }

  render () {
    this.el.innerHTML = this._RenderTemplate()
    $('input#parse-as-delimited-file-checkbox').on('change', () => this._onCheckBoxChange())
    return this
  }

  _RenderTemplate () {
    const ParseAsDelimitedFileEnabled = parseInt(this.data.value)
    if (ParseAsDelimitedFileEnabled) {
      return template(CheckBoxTemplate)({ checked: 'checked' })
    } else {
      return template(CheckBoxTemplate)({ checked: '' })
    }
  }

  _onCheckBoxChange () {
    const status = $('input#parse-as-delimited-file-checkbox').is(':checked')
    const value = status ? 1 : 0
    this.setValue(value)
  }

}

export default ParseAsDelimitedFileInput
